*{
  text-decoration: none !important;
}
.products-fields{
  background-color: #f9f9f9;
}
.items {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 0 100px 35px 100px;
}

.items li {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: white;
  border-radius: 4px;
  width: 25%;
  min-width: 285px;
  height: 400px;
  padding: 40px 15px;
  margin: 20px;
  overflow: hidden;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px,
  rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
}

.shoeimage {
  max-width: 100%;
  max-height: 200px;
}
.nameAndStar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  margin-top: 15px;
}

.nameAndStar strong {
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bolder;
  color: #17353d;
  margin: 10px 0;
}

.nameAndStar span {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fae5d3;
  border-radius: 2px;
  padding: 2px 3px;
}

.nameAndStar span img {
  width: 15px;
  height: 15px;
}

.nameAndStar span small {
  color: #f79b34;
  margin: 0;
}

.items li h3 {
  color: #e74c3c;
  font-family: Arial, Helvetica, sans-serif;
  font-size: x-large;
  font-weight: bolder;
  margin: 0;
  margin-top: 10px;
}

.items li h4 {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: small;
  color: #17353d;
  margin-bottom: 0;
}
.detailedImage{
  width: 374px;
  height: 329px;
}
.detailedcont{
  border : 1px solid black;
  min-height: 80vh;
}
.detailedPara {
  font-style: normal;
font-weight: 400;
font-size: 20px;
line-height: 27px;

color: #17353D;
}
.detailedPrice {
  font-family: 'Noto Sans';
font-style: normal;
font-weight: 600;
font-size: 30px;
line-height: 41px;
color: #D01E1E;
}
.detailedQty{
  font-family: 'Noto Sans';
font-style: normal;
font-weight: 600;
font-size: 20px;
line-height: 27px;

color: #000000;
}
.detailedDelivery{
  font-family: 'Noto Sans';
font-style: normal;
font-weight: 400;
font-size: 18px;
line-height: 25px;
color: #17353D;

opacity: 0.5;
}
.detailednameAndStar strong {
  font-size: 50px;
line-height: 68px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bolder;
  color: #17353d;
  margin: 10px 0;
}

.detailednameAndStar span {
  background-color: #fae5d3;
  border-radius: 2px;
  padding: 2px 3px;
  border:1px solid #fae5d3;
  
}
.buynowBtn {
  margin-left: 42% !important;
}
.detailednameAndStar span img {
  width: 20px;
  height: 20px;
}

.detailednameAndStar span small {
  color: #f79b34;
  height: 30px;

  margin: 0;
}
/* -- quantity box -- */

.quantity {
  display: inline-block; }
 
 .quantity .input-text.qty {
  width: 35px;
  height: 39px;
  padding: 0 5px;
  text-align: center;
  background-color: transparent;
  border: 1px solid #efefef;
 }
 
 .quantity.buttons_added {
  text-align: left;
  position: relative;
  white-space: nowrap;
  vertical-align: top; }
 
 .quantity.buttons_added input {
  display: inline-block;
  margin: 0;
  vertical-align: top;
  box-shadow: none;
 }
 
 .quantity.buttons_added .minus,
 .quantity.buttons_added .plus {
  padding: 7px 10px 8px;
  height: 41px;
  background-color: #ffffff;
  border: 1px solid #efefef;
  cursor:pointer;}
 
 .quantity.buttons_added .minus {
  border-right: 0; }
 
 .quantity.buttons_added .plus {
  border-left: 0; }
 
 .quantity.buttons_added .minus:hover,
 .quantity.buttons_added .plus:hover {
  background: #eeeeee; }
 
 .quantity input::-webkit-outer-spin-button,
 .quantity input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  margin: 0; }
  
  .quantity.buttons_added .minus:focus,
 .quantity.buttons_added .plus:focus {
  outline: none; }
 

/* Media Queries */
@media screen and (max-width: 1280px){#home{margin-left: 30%;}}
@media screen and (max-width: 1000px) {
    .items {
      padding: 0px !important;
    }
    .items li {
      width: 35%;
    }
    .product-field {
      padding: 0px !important;
    }
  }
@media screen and (max-width: 740px) {
  .topnav a:not(:first-child),
  .dropdown .dropbtn {
    display: none;
  }
  .topnav a.icon {
    float: right;
    display: block;
  }
  #home{margin-left: 0;}
  #brand{margin-left: 0;}
  .buynowBtn{margin-left: 0;}
}

@media screen and (max-width: 740px) {
  .topnav.responsive {
    position: relative;
  }
  .topnav.responsive .icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
  .topnav.responsive {
    float: none;
  }
  .topnav.responsive {
    position: relative;
  }
  .topnav.responsive {
    display: block;
    width: 100%;
    text-align: left;
  }
}


@media screen and (max-width: 650px) {
  .product-field {
    padding: 0px !important;
  }
  .items {
    padding: 0px !important;
  }
  .items li {
    width: 50%;
    padding-left: auto !important;
    padding-right: auto !important;
  }
}
@media screen and (max-width: 510px){
  #totalProductsShown{font-size:20px;}
}
@media screen and (max-width: 450px){
  #totalProductsShown{font-size:15px;}
}